body {
  background: lightcyan;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}
.root {
  width: 800px;
  height: 500px;
  display: flex;
  overflow: hidden;
}
.root img {
  width: 800px;
  height: 500px;
  border-radius: 10px;
  transition: 1s linear;
}
.inputs, h1 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.inputs {
  position: relative;
  bottom: 30px;
}
.inputs label input {
  width: 20px;
  height: 20px;
}
.inputs label:not(:last-child) {
  margin-right: 20px;
}